export default "import { NgModule } from \"@angular/core\";\nimport { FormsModule, ReactiveFormsModule } from \"@angular/forms\";\nimport { RouterModule } from \"@angular/router\";\nimport { DEMO_PATH_TOKEN, NuiDividerModule, NuiDocsModule, NuiSelectModule } from \"@nova-ui/bits\";\n\nimport { HighlightExampleComponent } from \"./highlight.example.component\";\n\nconst routes = [{\n    path: \"\",\n    component: HighlightExampleComponent,\n}];\n\n@NgModule({\n    imports: [\n        NuiDividerModule,\n        FormsModule,\n        ReactiveFormsModule,\n        NuiDocsModule,\n        RouterModule.forChild(routes),\n        NuiSelectModule,\n    ],\n    declarations: [\n        HighlightExampleComponent,\n    ],\n    providers: [\n        {\n            provide: DEMO_PATH_TOKEN,\n            useFactory: () => (<any>require).context(`!!raw-loader!./`, true, /.*\\.(ts|html|less)$/),\n        },\n    ],\n    exports: [\n        RouterModule,\n    ],\n})\nexport class HighlightModule {\n}\n";